// =============================================================================
// > LAYOUTS: SITE HEADER
// =============================================================================

.site-header {

    padding: 30px 0;
    line-height: 50px;

    // ==================================================
    // > BRAND
    // ==================================================
    &__brand {
        display: block;
        @include size(200px, 50px);

        @include font(20px bold uppercase); }

    // ==================================================
    // > NAV
    // ==================================================
    &__nav {
        @include flex(row nowrap, space-between, center); }

    // ==================================================
    // > MAIN MENU
    // ==================================================
    &__menu {
        @include flex(row nowrap, space-around, center);
        position: relative;
        z-index: 10;

        &__item {
            position: relative;
            z-index: 10; }

        &__link {
            @include font(text);
            padding: 0 1.5em;
            &:hover {
                @include font(primary); } }

        .sub-menu {
            display: none; } }



    // ========== INDICATOR ========== //
    &__menu__indicator {
        @include absolute(bottom 0 left 0);
        @include size(0, 5px);
        background: color(primary);
        transition: all .5s easing(bounce); } }



