// =============================================================================
// > LAYOUTS: MODULES - LISTS
// =============================================================================

ol,
ul {
    list-style: none; }

ul:not([class]),
ol:not([class]),
.list {
    margin: 1em 0;
    padding-left: 1.2em;

    // EMBEDED
    & & {
        margin-top: .5em; } }

ul:not([class]) > li,
ol:not([class]) > li,
.list > li,
.list__item {
    position: relative;
    margin-bottom: .4em;
    &::before {
        @include font(primary);
        font-size: .9em;
        line-height: 1.8em;
        @include absolute(left -1.2em top 0); }

    &[data-collapsable] {
        padding-right: 1.5em; } }

// =============================================================================
// > BULLETS
// =============================================================================

// ========== COLORS ========== //
@each $slug, $value in $colors {
    .list--bullets-#{$slug} > li::before {
        color: $value !important; } }

// ========== NUMBERS / OL ========== //
ol:not([class]),
.list--numbers {
    counter-reset: item; }

ol:not([class]),
.list--numbers {
    & > .list__item::before, & > li::before {
        content: counter(item)".";
        counter-increment: item;
        @include font(bold); } }

// ========== DEFAULT / UL ========== //
ul:not([class]),
.list--circle {
    &,
    & & {
        & > .list__item::before,
        & > li::before {
            @include fa(circle);
            font-size: .5em;
            line-height: 2.8em;
            left: -1.75em; } }
    & & {
        & > .list__item::before,
        & > li::before {
            @include far(circle); } } }


// ========== CARRETS ========== //
.list--carrets {
    & > .list__item::before,
    & > li::before {
        @include fa(caret-right); } }

// ========== ANGLES ========== //
.list--angles {
    & > .list__item::before,
    & > li::before {
        @include fa(angle-right); } }


// ========== ARROWS ========== //
.list--arrows {
    padding-left: 1.35em;
    & > .list__item::before,
    & > li::before {
        @include fa(arrow-right);
        left: -1.35em; } }

// ========== CHECKS ========== //
.list--checks {
    padding-left: 1.35em;
    & > .list__item::before,
    & > li::before {
        @include fa(check-circle);
        font-size: 1em;
        line-height: unset;
        @include font(secondary);
        left: -1.35em; } }

// ========== CHOICES (radio/checkbox) ========== //
.list--choices {
    padding-left: 1.5em;
    & > .list__item input.list__bullet,
    & > li input.list__bullet {
        @include absolute(top 0 left -1.5em); }

    & > .list__item .list__label,
    & > li .list__label {
        margin: 0;
        @include font(regular); } }

// =============================================================================
// > COLLAPSABLE
// =============================================================================
.list__item__collapse {
    @include absolute(top 0 right 0);
    @include size(1.5em);
    cursor: pointer;
    border-radius: 50%;
    transition: all .35s ease;
    &::before {
        content: "";
        @include size(100em, 100%);
        @include absolute(top 0 right 0); }
    &::after {
        @include fa(angle-down);
        vertical-align: middle;
        @include font(center medium-grey);
        transition: transform .35s ease;
        transform: translateY(5%);
        @extend %cover; }

    &:hover {
        background: color(primary);
        &::after {
            @include font(white); } }

    .is-open > &::after {
        transform: rotate(-180deg) translateY(5%); } }




// =============================================================================
// > DISPOSITIONS
// =============================================================================
.list--vertical-centered {
    @include flex(column nowrap, center, center); }

.list--horizontal {
    @include flex(row nowrap);
    margin-left: -10px;
    margin-right: -10px;
    & > li {
        padding-left: 10px;
        padding-right: 10px; } }



