// =============================================================================
// > LAYOUTS: SITE FOOTER
// =============================================================================

.site-footer {
    clear: both;

    // =============================================================================
    // > TOP
    // =============================================================================
    &__top {
        background: color(light-grey);
        padding: 1em 0; }

    // =============================================================================
    // > BOTTOM
    // =============================================================================
    &__bottom {
        padding: 5px 0;
        background: white;
        @include font(12px #7B7E7F);
        @include screen(xs) {
            padding: 10px 0;
            @include font(14px 30px); } }

    &__menu {
        @include flex(row nowrap);
        margin: 0 -10px;
        @include screen(xs) {
            flex-direction: column; }
        &__item {
            padding: 0 10px; }

        &__link {
            @include font(#7B7E7F);
            &:hover {
                @include font(primary); } } }


    &__copyright {
        margin: 0; } }



