// =============================================================================
// > LAYOUTS: TEMPLATES - SINGLE
// =============================================================================

.single {

    // ==================================================
    // > NAV
    // ==================================================
    &__nav {
        padding: 20px 0;
        overflow: hidden;
        @include font(15px);

        @include screen(xs) {
            @include flex(column nowrap);
            @include font(16px medium-grey); }

        &--top {
            border-bottom: 2px solid color(light-grey); }
        &--bottom {
            border-top: 2px solid color(light-grey); }

        &__link {
            @include font(text);
            transition: all .25s ease;
            @include flex(row nowrap, null, center);
            @include screen(xs) {
                padding: 8px 0; }

            &::before {
                @include font(12px 22px center primary);
                @include size(22px);
                border: 1px solid color(primary);
                border-radius: 50%;
                margin: 0 .5em;
                transition: all .25s ease;
                @include screen(xs) {
                    @include size(28px);
                    @include font(15px 28px);
                    margin-right: 1em; } }

            &:hover {
                @include font(primary);
                &::before {
                    background: color(primary);
                    color: color(white); } }

            &--archive {
                float: left; }

            &--prev,
            &--next {
                float: right; }

            &--next {
                @include screen(null, xs) {
                    flex-direction: row-reverse;
                    margin-left: 20px; } }

            &--archive::before {
                @include fa(list); }
            &--prev::before {
                @include fa(angle-left); }
            &--next::before {
                @include fa(angle-right); } } }



    // ==================================================
    // > CONTENT
    // ==================================================
    &__content {
        padding: 40px 300px 40px 0;
        @include screen(md) {
            padding-right: 150px; }
        @include screen(sm) {
            padding-right: 0; } }

    // =============================================================================
    // > TYPES
    // =============================================================================

    /* NEWS */
 }    // &-news &



