// =============================================================================
// > UTILS - MEDIA QUERIES
// =============================================================================

// ==================================================
// > SCREEN
// > Usage :
// > +screen(md)
// >    color: red
// ==================================================

@mixin screen($max_width, $min_width: null, $max_height: null, $min_height: null) {

    $query: "screen";

    // max width
    @if breakpoint($max_width) != null {
        $query: $query + " and (max-width: #{breakpoint($max_width)})"; }

    // min width
    @if breakpoint($min_width) != null {
        $query: $query + " and (min-width: #{breakpoint($min_width)})"; }

    // max height
    @if breakpoint($max_height) != null {
        $query: $query + " and (max-height: #{breakpoint($max_height)})"; }

    // min height
    @if breakpoint($min_height) != null {
        $query: $query + " and (min-height: #{breakpoint($min_height)})"; }

    @if $query == "screen" {
        @content; }
    @else {
        @media #{$query} {
            @content; } } }



// ==================================================
// > PRINT
// ==================================================
@mixin print {
    @media print {
        @content; } }



