// =============================================================================
// > MODULE : NAVIGATION - SOCIAL
// =============================================================================

.social {

    margin: 0 .5em;
    @include flex(row nowrap);

    &__item {
        padding: 0 .5em; }

    &__link {
        display: block;
        @include font(22px primary);
        transition: all .35s ease;
        &:hover {
            @include font(secondary); } } }




