
// =============================================================================
// > CONFIG
// =============================================================================
$fa-font-path:                "../../styles/assets/vendors/fontawesome/webfonts" !default;
$fa-font-size-base:           16px !default;
$fa-css-prefix:               fa !default;
$fa-version:                  "5.6.3" !default;
$fa-border-color:             #eee !default;
$fa-inverse:                  #fff !default;
$fa-li-width:                 2em !default;
$fa-fw-width:                 (20em / 16);


// =============================================================================
// > IMPORTS
// =============================================================================

@import "fa-icons";
// @import "mixins"
@import "core";
// @import "larger"
// @import "fixed-width"
// @import "list"
// @import "bordered-pulled"
// @import "animated"
// @import "rotated-flipped"
// @import "stacked"
@import "icons";
// @import "screen-reader"
@import "sources";


// =============================================================================
// > VARIANTS
// =============================================================================


// ========== SOLID ========== //
@mixin fa($icon_var: false) {
    font-family: "Font Awesome";
    font-weight: 900;
    @if $icon_var {
        content: fa-content($icon_var); } }

.fa,
.fas {
    @include fa(); }


// ========== REGULAR ========== //
@mixin far($icon_var: false) {
    font-family: "Font Awesome";
    font-weight: 400;
    @if $icon_var {
        content: fa-content($icon_var); } }

.far {
    @include far(); }


// ========== LIGHT ========== //
@mixin fal($icon_var: false) {
    font-family: "Font Awesome";
    font-weight: 300;
    @if $icon_var {
        content: fa-content($icon_var); } }

.fal {
    @include fal(); }


// ========== BRANDS ========== //
@mixin fab($icon_var: false) {
    font-family: "Font Awesome Brands";
    @if $icon_var {
        content: fa-content($icon_var); } }

.fab {
    @include fab(); }


// ========== DUOTONE ========== //
@mixin fad($icon_var: false) {
    font-family: "Font Awesome Duotone";
    @if $icon_var {
        content: fa-content($icon_var); } }

.fad {
    @include fad(); }



